.cart {
    animation: items-appear 1s ease-out forwards;
}
.cart-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px solid #525252;
    padding: 1rem 0;
    margin: 1rem 0;
    letter-spacing: 0;
    font-size: 16px;
}

.info {
    margin-left: 12px;
}

.cart-item h2 {
    margin: 0 0 0.5rem 0;
    color: #363636;
}

.summary {
    width: 15rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.price {
    font-weight: bold;
    color: #8a2b06;
}

.quantity {
    font-weight: bold;
    border: 1px solid rgb(99, 99, 99);
    padding: 0.25rem 0.75rem;
    border-radius: 6px;
    color: #363636;
}

.actions {
    display: flex;
    flex-direction: column;
}

@media (min-width: 768px) {
    .actions {
        flex-direction: row;
    }
}

