.item-info__container {
    width: 50em;
    display: inline-flex;
}

.item-info__info {
    padding: 0 1em;
    width: 100%;
}

.title {
    font-weight: 600;
    letter-spacing: 0em;
    font-size: 1em;
}

.size {
    font-weight: 300;
    font-size: 0.8em;
    letter-spacing: 0;
}

.color {
    font-weight: 300;
    font-size: 0.8em;
    letter-spacing: 0;
}
.quantityContainer {
    display: inline-flex;
    border-radius: 12px;
    border: #c0c0c0 1px solid;
    padding: 6px 0;
}

.btnQuantity {
    display: flex;
    align-items: center;
}

.quantitybutton__left {
    vertical-align: middle;
    color: #969696;
    border: 0;
    font-size: 1em;
    background-color: transparent;
    padding: 0 0 0 12px;
    cursor: pointer;
}
.quantitybutton__right {
    color: #969696;
    vertical-align: middle;
    font-size: 1em;
    border: 0;
    background-color: transparent;
    padding: 0px 12px 0 0;
    cursor: pointer;
}
.quantitylabel {
    font-size: 0.7em;
    padding: 0em 1em;
    border-radius: 0;
}
.col1 {
    margin-bottom: 0;
}

.col1 p {
    margin-bottom: 0 !important;
}

.col3 {
    margin-top: 0.5em;
}

