.fill {
    width: 100px;
    height: 100px;

    background-color: #f7f7f7;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    margin-top: 8px;
}
.fill img {
    width: 80%;
    height: auto;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}
